body {
  /* Variables */
  --font-size: 1.2em;
  --font-family: "Quicksand", sans-serif;
  --hover-color: rgb(100, 82, 255);
  /* End */

  font-family: var(--font-family);
  font-size: var(--font-size);
  background-color: rgb(31, 37, 53);
  color: wheat;
}

header#Header {
  width: 100%;
  height: 5em;
  background-color: rgba(255, 255, 255, 0.05);
  border-bottom: 1px solid wheat;
}

header h1 {
  float: left;
  margin-left: 1em;
  width: auto;
  padding-top: 0.5em;
  font-size: calc(var(--font-size) * 2);
  font-weight: bold;
  text-align: center;
}

footer#Footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 5em;
  background-color: rgba(255, 255, 255, 0.05);
  border-top: 1px solid wheat;
}

#MainContainerComponent {
  margin: auto;
  margin-top: 12.5em;
  height: 20em;
  width: 50em;
}

#ImageComponent {
  float: left;
  width: 10em;
  height: 10em;
}

#Image {
  width: calc(10em - 4px);
  border-radius: 15em;
  border: 2px solid rgb(245, 222, 179);
}

#ProfileComponent {
  margin-left: 1em;
  float: left;
  width: auto;
  height: 100%;
}

#NameComponent {
  margin-top: 0.25em;
  margin-bottom: 0.25em;
  font-size: calc(var(--font-size) * 2);
  text-decoration: underline;
  text-decoration-color: whitesmoke;
}

#InfoComponent {
  display: block;
  height: 1.2em;
  margin-top: 0.2em;
  color: rgba(245, 222, 179, 0.8);
}

#Icon {
  float: left;
  padding: 0.1em;
  margin-right: 0.3em;
  width: 1em;
  height: 1em;
}
